const images = document.querySelectorAll('img');
const options = {
  root: null,
  rootMargin: '0px',
  threshold: 0.1
}
function handleImg(myImg, observer){
  myImg.forEach(myImgSingle => {
    if(myImgSingle.intersectionRatio > 0){
      loadImage(myImgSingle.target);
    }
  })
}
function loadImage(image){
  image.src = image.getAttribute('data');
}
const observer = new IntersectionObserver(handleImg, options);
images.forEach(img =>{
  observer.observe(img);
})



const catalogBtn = document.querySelector('#catalogBtn');
const catalogList = document.querySelector('.catalog__list');
catalogBtn.addEventListener('click', (e)=>{
  e.preventDefault();
  catalogList.style.height = `${document.querySelector('.catalog__list-wrap').offsetHeight}px`;
  catalogBtn.remove();
});

let swiper = new Swiper(".swiper", {
  slidesPerView: 3,
  centeredSlides: true,
  initialSlide: 1,
});

const loadmore = document.querySelector('.interiors__btn-b');

let currentItems = 6;

loadmore.addEventListener('click', (e)=>{
  e.preventDefault();
  const elementList = [...document.querySelectorAll('.interiors__grid-item')]
  e.target.classList.add('show-loader');

  for(let i = currentItems; i < currentItems + 6;i++){
    setTimeout(function(){
      e.target.classList.remove('show-loader');
      if(elementList[i]){
        elementList[i].style.display = 'inline-block';
      }
    },100)
  }
  currentItems += 6;

  if(currentItems >= elementList.length){
    event.target.classList.add('loaded')
  }
})


